import Vue from 'vue'
import Router from 'vue-router'

import firstChild from '@/components/firstChild.vue'
import secondChild from '@/components/secondChild.vue'
import thirdChild from '@/components/thirdChild.vue'
import fourthChikd from '@/components/fourthChikd.vue'
import fifthChild from '@/components/fifthChild.vue'

Vue.use(Router)

export default new Router({
  routes: [
    { path: '/',component: firstChild},
    { path: '/second',component: secondChild},
    { path: '/third',component: thirdChild},
    { path: '/fourth',component: fourthChikd},
    { path: '/fifthChild',component: fifthChild},
  ]
})



