<template>
	<div class="searchDiv">
		<div>
		    	<span>广州</span>
		    	<input type="text" placeholder="搜索品牌、级别"/>
		    	<div>
		    		<img src="/static/img/camera.png" />
		    		<img src="/static/img/message.png" />
		    	</div>
   	  	 </div>
	</div>
</template>

<script>
	export default{
		data(){return{}}
	}
</script>

<style scoped>
	.searchDiv{
		width: 11.52rem;
   		height: 1.92rem;
   		padding: 1px 0.45rem 0px 0.45rem;
   		color: white;
   		background: url(/static/img/25.png)no-repeat;
   		background-size: cover;
   	}
   	.searchDiv>div{
   		margin-top: .8rem;
   	}
   	.searchDiv>div>span{
   		width: 2.19rem;
   		height: 0.8rem;
   		line-height: .8rem;
   		font-size: .45rem;
   		display: inline-block;
   		float: left;
   	}
   	.searchDiv>div>input{
   		width: 7.18rem;
   		height: .80rem;
   		text-indent: 2.5rem;
   		font-size: .4rem;
   		border: none;
   		border-radius: 0.5rem;
   		background-image: url(/static/img/searcg.png);
   		background-repeat: no-repeat;
   		background-position: 1.35rem 50%;
   		background-size: .4rem;
   		background-color: rgba(255,255,255,.3);
   		float: left;
   		outline: none;
   		color: white;
   	}
 	 .searchDiv>div>input::-webkit-input-placeholder{
 	 	color: white;
 	 }
   	.searchDiv>div>div{
   		width: 1.4rem;
   		height: .55rem;
   		float: right;
   		padding: .13rem 0px .13rem .4rem;
   	}
   	.searchDiv>div>div>img{
   		width: .56rem;
   		height: .5rem;
   		float: left;
   	}
   	.searchDiv>div>div>img:nth-of-type(1){
   		margin-right: .2rem;
   	}
</style>


