<template>
	<div>
		<div class="banerDiv">
			<div class="swiper-container">
				<div class="swiper-wrapper">
					<div class="swiper-slide" v-for="i in navList">
						<img :src=i />
					</div>
				</div>
				<!-- 分页器 -->
				<div class="swiper-pagination"></div>
			</div>
			<div class="searchHead">
				<span>广州</span>
				<input type="text" placeholder="搜索品牌、级别" />
				<div>
					<img src="/static/img/camera.png" />
					<img src="/static/img/message.png" />
				</div>
			</div>
		</div>
		<div class="ulDiv">
			<ul>
				<li v-for="i in ulDivList">
					<img :src=i.img>
					<p>{{i.info}}</p>
				</li>
			</ul>
		</div>
		<div class="recommend">
			<h2><img src="/static/img/7.png" /> 热门推荐</h2>
			<ul>
				<li v-for="i in hotList">
					<img :src=i.img />
					<p>{{i.info}}</p>
				</li>
			</ul>
		</div>
		<div class="showList">
			<ul>
				<li>
					<p>A</p>
				</li>
				<li v-for="i in showListA">
					<img :src=i.img />
					<span>{{i.info}}</span>
				</li>
				<li>
					<p>B</p>
				</li>
				<li v-for="i in showListB">
					<img :src=i.img />
					<span>{{i.info}}</span>
				</li>
				<li>
					<p>C</p>
				</li>
				<li v-for="i in showListC">
					<img :src=i.img />
					<span>{{i.info}}</span>
				</li>
			</ul>
		</div>

		<div class="sideLlist">
			<ul>
				<li v-for="i in sideLlist">{{i}}</li>
			</ul>
		</div>
		<public-child class="publicChild"></public-child>
	</div>
</template>

<script>
	import publicChild from './publicChild.vue';

	export default {
		components: {
			publicChild
		},
		mounted() {
			var mySwiper = new Swiper('.swiper-container', {
				loop: true,
				pagination: '.swiper-pagination',
				autoplay:4000,
				autoplayDisableOnInteraction:false,
			});
			
		},
		methods: {
				
		},
		data() {
			return {
				navList: ['/static/img/1.png', '/static/img/1-1.png', '/static/img/1-2.png'],
				ulDivList: [{img: "/static/img/2.png",info: "条件选车"},
					{img: "/static/img/3.png",info: "销量排行"},
					{img: "/static/img/4.png",info: "汽车资讯"},
					{img: "/static/img/5.png",info: "贷款买车"},
					{img: "/static/img/6.png",info: "今日疯抢"}
				],
				hotList: [{img: "/static/img/8.png",info: "斯柯达"},
					{img: "/static/img/9.png",info: "宝马"},
					{img: "/static/img/10.png",info: "吉列汽车"},
					{img: "/static/img/11.png",info: "本田"},
					{img: "/static/img/12.png",info: "长安"},
					{img: "/static/img/13.png",info: "传祺GS8"},
					{img: "/static/img/14.png",info: "力帆X80"},
					{img: "/static/img/15.png",info: "杰德"},
				],
				showListA: [{img: "/static/img/16.png",info: "奥迪"
					},
					{img: "/static/img/17.png",info: "阿斯顿·马丁"},
					{img: "/static/img/18.png",info: "阿尔法·罗密欧"},
					{img: "/static/img/19.png",info: "ALPINA"},
					{img: "/static/img/20.png",info: "ARCFOX"},
				],
				showListB: [{img: "/static/img/21.png",info: "本田"},
					{img: "/static/img/22.png",info: "别克"},
					{img: "/static/img/23.png",info: "奔驰"},
					{img: "/static/img/24.png",info: "宝马"}
				],
				showListC: [{img: "/static/img/21.png",info: "本田"},
					{img: "/static/img/17.png",info: "阿斯顿·马丁"},
					{img: "/static/img/18.png",info: "阿尔法·罗密欧"},
					{img: "/static/img/19.png",info: "ALPINA"},
					{img: "/static/img/20.png",info: "ARCFOX"},
				],
				sideLlist: ['#', "A", 'B', "C", "D", "F", "G", 'H', 'J', 'K', 'L', 'M', 'N', 'O', 'P', 'Q', 'R', 'S', 'T', 'W', 'X', 'Y', 'Z']
			}
		}
	}
</script>

<style scoped>
	.banerDiv {
		height: 4.98rem;
		position: relative;
	}
	
	.swiper-container {
		height: 4.98rem;
		border: 1px solid #000;
	}
	
	.swiper-container img {}
	
	.swiper-slide>img {
		width: 100%;
		height: 100%;
	}
	/***************************************************************/
	
	.searchHead {
		height: .81rem;
		padding: 0px 0.45rem 0px 0.45rem;
		color: white;
		margin-top: .8rem;
		z-index: 2;
		position: absolute;
		top: 0;
	}
	
	.searchHead>span {
		width: 2.19rem;
		height: 0.8rem;
		line-height: .8rem;
		font-size: .45rem;
		display: inline-block;
		float: left;
	}
	
	.searchHead>input {
		width: 7.18rem;
		height: .80rem;
		text-indent: 2.5rem;
		font-size: .4rem;
		border: none;
		border-radius: 0.5rem;
		background-image: url(/static/img/searcg.png);
		background-repeat: no-repeat;
		background-position: 1.35rem 50%;
		background-size: .4rem;
		background-color: rgba(255, 255, 255, .3);
		float: left;
		outline: none;
		color: white;
	}
	
	.searchHead>input::-webkit-input-placeholder {
		color: white;
	}
	
	.searchHead>div {
		width: 1.4rem;
		height: .55rem;
		float: right;
		padding: .13rem 0px .13rem .4rem;
	}
	
	.searchHead>div>img {
		width: .56rem;
		height: .5rem;
		float: left;
	}
	
	.searchHead>div>img:nth-of-type(1) {
		margin-right: .2rem;
	}
	/***************************************************************/
	
	.ulDiv {
		height: 2.45rem;
		background-color: white;
	}
	
	.ulDiv>ul {
		font-size: .32rem;
	}
	
	.ulDiv img {
		width: 1.45rem;
		height: 1.52rem;
	}
	
	.ulDiv>ul>li {
		width: 1.45rem;
		height: 2.45rem;
		/*border: 1px solid #ADD8E6;*/
		float: left;
		margin: 0px .45rem 0px .45rem;
	}
	
	.recommend {
		height: 5.95rem;
		margin-top: .3rem;
		background-color: white;
	}
	
	.recommend>h2 {
		font-size: .42rem;
	}
	
	.recommend>h2 img {
		width: .53rem;
		height: .53rem;
		vertical-align: middle;
		margin-left: .45rem;
		margin-right: .28rem;
	}
	
	.recommend>ul>li {
		font-size: .35rem;
		float: left;
	}
	
	.recommend>ul>li:nth-of-type(1),
	.recommend>ul>li:nth-of-type(2),
	.recommend>ul>li:nth-of-type(3),
	.recommend>ul>li:nth-of-type(4),
	.recommend>ul>li:nth-of-type(5) {
		width: 1.49rem;
		height: 2.3rem;
		margin: 0px .45rem 0px 0.45rem;
		text-align: center;
	}
	
	.recommend>ul>li:nth-of-type(1)>img,
	.recommend>ul>li:nth-of-type(2)>img,
	.recommend>ul>li:nth-of-type(3)>img,
	.recommend>ul>li:nth-of-type(4)>img,
	.recommend>ul>li:nth-of-type(5)>img {
		width: 1.49rem;
		height: 1.53rem;
	}
	
	.recommend>ul>li:nth-of-type(6),
	.recommend>ul>li:nth-of-type(7),
	.recommend>ul>li:nth-of-type(8) {
		width: 2.52rem;
		height: 3rem;
		margin: 0px .72rem 0px 0.72rem;
		text-align: center;
	}
	
	.recommend>ul>li:nth-of-type(6)>img,
	.recommend>ul>li:nth-of-type(7)>img,
	.recommend>ul>li:nth-of-type(8)>img {
		width: 2.52rem;
		height: 2.22rem;
	}
	
	.showList {
		margin-top: .3rem;
		font-size: .43rem;
		padding-left: .45rem;
		background-color: white;
	}
	
	.showList>ul>li {
		font-size: .35rem;
	}
	
	.showList>ul>li>img {
		width: 1.09rem;
		height: 1.69rem;
		vertical-align: middle;
	}
	
	.showList>ul>li>span {
		font-size: .47rem;
		margin-left: .4rem;
	}
	
	.sideLlist {
		font-size: .26rem;
		color: #666;
		position: fixed;
		right: 0;
		top: 4.4rem;
	}
	
	.sideLlist>ul>li {
		width: .67rem;
		height: .28rem;
		text-align: center;
		margin-bottom: .4rem;
	}
	.publicChild{
		position: fixed;
		top: 0;
	}
</style>


