<template>
	<div>
		<div class="accountHead">
			<div>
				<img src="/static/img/people.png"/>
				<span>账号登录</span>
				<span>&gt;</span>
			</div>
		</div>
		<div class="orderList">
			<h4>我的订单</h4>
			<ul>
				<li v-for="i in orderList">
					<img :src=i.img />
					<p>{{i.info}}</p>
				</li>
			</ul>
		</div>
		
		<div class="individualList">
			<ul>
				<li v-for="i in individualList">
					<img :src=i.img />
					<span>{{i.info}}</span>
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
	export default{
		data(){
			return{
				orderList:[
					{img:"/static/img/58.png",info:"买新车"},
					{img:"/static/img/59.png",info:"今日疯抢"},
					{img:"/static/img/60.png",info:"超值特惠"},
					{img:"/static/img/61.png",info:"新车询价"},
					{img:"/static/img/62.png",info:"新车贷款"},
				],
				individualList:[
				{img:"/static/img/63.png",info:"我的社区"},
				{img:"/static/img/64.png",info:"签到领车币"},
				{img:"/static/img/65.png",info:"车币商城"},
				{img:"/static/img/66.png",info:"我的优惠券"},
				{img:"/static/img/67.png",info:"车型收藏"},
				{img:"/static/img/68.png",info:"浏览历史"},
				{img:"/static/img/69.png",info:"用户反馈"},
				{img:"/static/img/70.png",info:"设置"},
				]
				
			}
		}
	}
</script>

<style scoped>
	.accountHead{
		height: 4.18rem;
		background: url(/static/img/bg.png) no-repeat;
		background-size: cover;
		padding: 1px;
	}
	.accountHead>div{
		margin-top: 1.35rem;
		font-size: .45rem;
		color: white;
		padding-left: .5rem;
	}
	.accountHead>div>img{
		width: 1.98rem;
		height: 2.03rem;
		vertical-align: middle;
	}
	.accountHead>div>span:nth-of-type(1){
		margin-left: .55rem;
	}
	.accountHead>div>span:nth-of-type(2){
		margin-left: 5.84rem;
	}
	.orderList{
		height: 4.15rem;
		font-size: .36rem;
		background: white;
		margin-bottom: .31rem;
	}
	.orderList>h4{
		height: 1.24rem;
		line-height: 1.24rem;
		text-indent: .45rem;
	}
	.orderList>ul>li{
		width: 1.5rem;
		height: 2.92rem;
		float: left;
		margin: 0px .62rem 0px .36rem;
	}
	.orderList>ul>li>img{
		width: 1.4rem;
		height: 1.67rem;
	}
	.orderList>ul>li>P{
		text-align: center;
	}
	.individualList{
		height: 11.98rem;
		background: white;
		padding-left: .36rem;
	}
	.individualList>ul>li{
		height: 1.49rem;
		font-size: .4rem;
		color: #666;
	}
	.individualList>ul>li>img{
		width: 1.07rem;
		height: 1.49rem;
		vertical-align: middle;
	}
</style>


